import { Divider, Paper, Stack, TextField, Typography } from "@mui/material";
import React, { FC } from "react";
import TutorialTip from "../../Tutorial/TutorialTip";
import styled from "@emotion/styled";

interface PlayerInformationProps {
  /** Player's ID */
  id: number;
  step: number;
  cumulativePoints: number;
  totalScore: number;
  /** Player's comment */
  comment?: string;
  showComment?: boolean;
  /** show tutorial tip */
  showTutorialScore?: boolean;
  showTutorialComment?: boolean;
  showTutorialTotalScore?: boolean;
  /** Callback to handle tutorial tip close */
  onTutorialCommentClose?: () => void;
  onTutorialClose?: () => void;
  showCumulativePoints?: boolean;
  showTotalPoints?: boolean;
}

const Item = styled(Paper)(() => ({
  padding: 0,
  elevation: 0,
  textAlign: "left",
}));

const PlayerInfoItem: FC = ({ children }) => {
  return (
    <Item elevation={0}>
      {children}
      {/*<Divider/>*/}
    </Item>
  );
};

export const PlayerInformation: FC<PlayerInformationProps> = (props) => {
  const {
    showComment = true,
    showTutorialScore = false,
    showTutorialComment = false,
    showTutorialTotalScore = false,
    showCumulativePoints = true,
    showTotalPoints = true,
  } = props;
  return (
    <Stack spacing={0} sx={{ paddingTop: "20px" }}>
      {(showCumulativePoints || showTotalPoints) && (
        <>
          {showCumulativePoints && (
            <PlayerInfoItem>
              <TutorialTip
                tutorialId={"practice_step_score"}
                isTutorial={showTutorialScore}
                isShowTip={false}
                onTutorialClose={props.onTutorialClose}
                placement={"right"}
              >
                <Typography variant="h5" component="div">
                  {props.cumulativePoints} Points
                </Typography>
              </TutorialTip>
              <Typography variant="subtitle1" component="div">
                on current Network
              </Typography>
            </PlayerInfoItem>
          )}
          {/* {showTotalPoints && (
            <PlayerInfoItem>
              <TutorialTip
                tutorialId={"practice_total_score"}
                isTutorial={showTutorialTotalScore}
                isShowTip={false}
                onTutorialClose={props.onTutorialClose}
                placement={"right"}
              >
                <Typography variant="subtitle1" component="div">
                  Total: {props.totalScore}
                </Typography>
              </TutorialTip>
            </PlayerInfoItem>
          )} */}
        </>
      )}
      {showComment ? (
        <PlayerInfoItem>
          <Typography gutterBottom variant="subtitle1" component="div">
            Player {props.id} comment:
          </Typography>

          <TutorialTip
            tutorialId={"social_learning_observation_comment"}
            isTutorial={showTutorialComment}
            isShowTip={false}
            onTutorialClose={props.onTutorialCommentClose}
            placement={"right"}
          >
            <TextField
              id="outlined-multiline-static"
              // label=""
              multiline
              fullWidth
              rows={10}
              InputProps={{ readOnly: true }}
              defaultValue={props.comment ? props.comment : "No comment"}
            />
          </TutorialTip>
        </PlayerInfoItem>
      ) : null}
    </Stack>
  );
};

export default PlayerInformation;
